import React from 'react';
import { FaEye } from 'react-icons/fa';
// import _ from 'lodash';

import * as styles from './TAIK54Card.module.scss';

const TAIK54Card = ({ inspiringPerson, showPopup }) => {
  const { country, countryFlag, name, image } = inspiringPerson;

  return (
    <>
      <div className={`card border-0 ${styles.card}`}>
        <div
          className={`article_image ${styles.card_image}`}
          style={{ backgroundImage: `url(${image})` }}
        >
          <div className={styles.flag} style={{ backgroundImage: `url(${countryFlag})` }} />
        </div>

        <div className={`border-0 ${styles.card_content}`}>
          <h4 className={styles.name}>{name}</h4>
          <hr className={styles.divider} />

          <div className='d-flex justify-content-between align-items-center'>
            <p className={styles.country_name}>{country}</p>
            <button type='button' onClick={() => showPopup(inspiringPerson)}>
              Story <FaEye className={styles.icon} size={24} />
            </button>
          </div>
        </div>
      </div>
    </>
  );
};

export default TAIK54Card;
