import React from 'react';

import * as styles from './ValueCard.module.scss';

const ValueCard = ({ icon, title, description }) => (
  <div className='col d-flex align-items-center justify-content-center mb-5 mb-lg-1'>
    <div
      className={`card h-100 bg-altsuperlight text-altdarker position-relative ${styles.card_body}`}
    >
      <div className={styles.icon_holder}>
        <i className={`${icon} text-primary`} />
      </div>

      <h2 className={styles.title}>{title}</h2>
      <p className={styles.description}>{description}</p>
    </div>
  </div>
);

export default ValueCard;
