import React from 'react';
import Slider from 'react-slick';
import { Heading } from '@commons';
import QuoteCard from './QuoteCard';

const Quotes = ({ quotes }) => {
  // quotes slide setup
  let slidesToShow = 3;
  if (quotes.length === 1) {
    slidesToShow = 1;
  } else if (quotes.length === 2) {
    slidesToShow = 2;
  }

  const carouselSettings = {
    autoplay: true,
    dots: true,
    accessibility: true,
    infinite: true,
    autoplaySpeed: 6000,
    speed: 900,
    slidesToShow,
    slidesToScroll: 3,
    arrows: false,
    responsive: [
      {
        breakpoint: 992,
        settings: {
          slidesToShow,
          slidesToScroll: 3,
          infinite: true,
        },
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 1,
        },
      },
      {
        breakpoint: 575,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          initialSlide: 1,
          dots: false,
        },
      },
    ],
  }; // end slide config

  return (
    <section className='d-flex flex-column sections bg-altlightest'>
      <div className='container'>
        <div className='row'>
          <Heading primary='Quotes' color='altdarker' />
        </div>

        <Slider {...carouselSettings}>
          {quotes &&
            quotes.map((quote) => <QuoteCard key={quote.node.id} quote={quote.node.frontmatter} />)}
        </Slider>
      </div>
    </section>
  );
};

export default Quotes;
