import React from 'react';
import moment from 'moment';
import _ from 'lodash';
import { Link } from 'gatsby';

import * as styles from './ArticleCard.module.scss';

const ArticleCard = ({ article }) => {
  const { category, title, image_url, author, date, excerpt, slug } = article.node.frontmatter;
  // const { category, title, image_url, author, date, excerpt } = article.node.frontmatter;

  return (
    <div className={`card border-0 ${styles.card}`}>
      <div
        className={`article_image ${styles.card_image}`}
        style={{ backgroundImage: `url(${image_url.childImageSharp.fluid.src})` }}
      >
        <button
          type='button'
          className={`bg-altlighter text-altdark border-0 rounded-pill ${styles.category_pill}`}
        >
          {`#${category}`}
        </button>
      </div>

      <div className={`border-0 ${styles.card_content}`}>
        <div
          className={`card-header border-0 bg-transparent d-flex justify-content-between p-0 ${styles.card_header}`}
        >
          <p className='text-altlight'>
            {`By `}
            <span className='fw-medium'>{author}</span>
          </p>

          <p className='text-altlight'>{moment(date).format('MMMM DD, YYYY')}</p>
        </div>
        <h5 className={`card-title ${styles.card_title}`}>
          <Link className='text-decoration-none text-altdarker' to={slug}>
            {title}
          </Link>
        </h5>
        <p className='card-text'>{_.truncate(excerpt, { length: 222 })}</p>
      </div>
    </div>
  );
};

export default ArticleCard;
