import React, { useEffect, useState } from 'react';
import Interviews from './Interviews';
import Quotes from './Quotes';

const Features = ({ data }) => {
  const [quotesValue, setQuotesValue] = useState([]);
  const [featuresValue, setFeaturesValue] = useState([]);

  async function filterQuotes() {
    const mdQuotes = await data.allMarkdownRemark.edges.filter(
      ({ node }) => node.frontmatter.content_type === 'quote',
    );
    return mdQuotes.reverse();
  }

  async function filterFeatures() {
    const mdFeatures = await data.allMarkdownRemark.edges.filter(
      ({ node }) => node.frontmatter.content_type === 'feature',
    );
    return mdFeatures;
  }

  useEffect(async () => {
    const finalQuotes = await filterQuotes();
    if (finalQuotes) {
      setQuotesValue(finalQuotes);
    }

    const finalFeatures = await filterFeatures();
    if (finalFeatures) {
      setFeaturesValue(finalFeatures);
    }
  }, []);
  return (
    <>
      <Interviews featureData={featuresValue} />
      <Quotes quotes={quotesValue} />
    </>
  );
};

export default Features;
