import React from 'react';

import * as styles from './SingleFeatured.module.scss';

const EducationFound = ({ title }) => (
  <>
    <div className={`${styles.banner_container}`}>
      <div className='column align-items-start'>
        <h1 className={`${styles.title} text-altdarker text-start`}>{title}</h1>

        <div className={`bg-altdarker rounded-pill ${styles.banner_divider} ms-0`} />
      </div>
    </div>
  </>
);

export default EducationFound;
