import React from 'react';
import { Link } from 'gatsby';

import * as styles from './ProjectCard.module.scss';

const ProjectCard = ({ title, description, link, icon, more, isDark, color, image }) => (
  <div className='col d-flex align-items-center justify-content-center'>
    <div
      style={{
        backgroundImage: `url(${image})`,
        backgroundPosition: 'center',
        backgroundSize: 'cover',
        backgroundRepeat: 'no-repeat',
      }}
      className={`card h-100 text-${color} ${styles.card_body}`}
    >
      <div
        className={`h-100 w-100 ${styles.card_content} ${
          isDark ? styles.coloured : styles.not_coloured
        }`}
      >
        <h2 className={`text-uppercase ${styles.card_title}`}>{title}</h2>

        <div className={`bg-${color} ${styles.card_divider}`} />

        <div className={`card-body ${styles.card_description}`}>{description}</div>

        <Link to={link} className={`btn btn-outline-${color} rounded-pill ${styles.card_button}`}>
          {more}
          <i className={`${icon} ${styles.btn_icon} ms-2`} />
        </Link>
      </div>
    </div>
  </div>
);

export default ProjectCard;
