import React, { useContext } from 'react';
import Helmet from 'react-helmet';
import { FaPlay } from 'react-icons/fa';
import { Heading } from '@commons';
import { GeneralContext } from '../../../context/GeneralContext';
import * as styles from './index.module.scss';

const OriginStory = () => {
  const modalValue = useContext(GeneralContext);

  const playHomeVideo = () => {
    modalValue.setHomeVideoModal(true);
  };

  return (
    <section className='flex-column sections container-fluid p-0'>
      <Helmet>
        <body className={modalValue.homeVideoModal ? 'body-noscroll' : null} />
      </Helmet>
      <Heading secondary='Our' primary='Origin Story' color='altdarker' center />
      <div
        className={`w-100 ${styles.Video_container}`}
        style={{
          backgroundImage:
            'url(https://images.unsplash.com/photo-1487546331507-fcf8a5d27ab3?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1170&q=80)',
        }}
      >
        <div className={styles.video_overlay}>
          <button
            type='button'
            className={styles.play_btn}
            onClick={playHomeVideo}
            aria-label='Opens popup video about our origins'
          >
            <span>
              <FaPlay size={24} />
            </span>
          </button>
        </div>
      </div>
    </section>
  );
};

export default OriginStory;
