import React from 'react';

import * as styles from './PillarCard.module.scss';

const PillarCard = ({ title, description, image }) => (
  <div className='col d-flex align-items-center justify-content-center text-white pillar-card'>
    <div
      style={{
        backgroundImage: `url(${image})`,
        backgroundPosition: 'center',
        backgroundSize: 'cover',
        backgroundRepeat: 'no-repeat',
      }}
      className={`card h-100 rounded rounded-3 ${styles.card_body}`}
    >
      <div className={`h-100 w-100 small ${styles.card_content} ${styles.coloured}`}>
        <h2 className={`text-uppercase ${styles.card_title}`}>{title}</h2>

        <div className={`bg-white ${styles.card_divider}`} />

        <div className={`card-body ${styles.card_description}`}>{description}</div>
      </div>
    </div>
  </div>
);

export default PillarCard;
