import { FaQuoteLeft } from 'react-icons/fa';
import React from 'react';
import * as styles from './QuoteCard.module.scss';

const QuoteCard = ({ quote }) => (
  <div className={`card px-4 position-relative ${styles.card}`}>
    <div className={`d-inline-block position-absolute rounded-circle ${styles.quote_icon}`}>
      <FaQuoteLeft size={24} />
    </div>

    <p className={`mb-3 ${styles.card_text}`}>{quote.quote}</p>

    <h6 className={`mt-auto mx-0 mb-0 text-end fw-medium ${styles.author}`}>
      {' '}
      &bull; {quote.author}
    </h6>
  </div>
);

export default QuoteCard;
