import React from 'react';

import { Heading } from '@commons';
import ValueCard from './ValueCard';

const values = [
  {
    icon: 'fas fa-globe-africa',
    title: 'Pan-Africanism',
    description:
      'TAIK is a pan-african organization. We subscribe to the idea that the African continent and its people can only thrive through unity.',
  },
  {
    icon: 'fas fa-praying-hands',
    title: 'Optimism',
    description: `We believe in a bright future for Africa and its people. That's what breeds our ambition and motivates our work for the continent.`,
  },

  {
    icon: 'fas fa-brain',
    title: 'Pragmatism',
    description: `TAIK's work is led by Africans living on the continent and the diaspora. We understand the problems first-hand and we are action-driven.`,
  },
];

const Values = () => (
  <div className='d-flex flex-column sections bg-altlightest'>
    <div className='container'>
      <Heading secondary='Our' primary='Values' color='altdarker' center />

      <div
        className='row row-cols-1 row-cols-md-2 row-cols-lg-3 justify-content-md-center'
        style={{ marginTop: '60px' }}
      >
        {values && values.map((value, index) => <ValueCard key={index} {...value} />)}
      </div>
    </div>
  </div>
);

export default Values;
