import React from 'react';
import moment from 'moment';
import _ from 'lodash';

import * as styles from './EpisodeCard.module.scss';

const EpisodeCard = ({ episode }) => {
  const { category, title, description, image, date } = episode;

  return (
    <div className={`card h-100 ${styles.card_body}`}>
      <div
        style={{ backgroundImage: `url(${image})` }}
        className={`card-img-top img-fluid ${styles.card_image}`}
        alt={title}
      />

      <div
        className={`card-header border-0 bg-transparent small position-relative ${styles.card_content}`}
      >
        <span className='text-altlight small'>{moment(date).format('MMMM DD, YYYY')}</span>

        <button
          type='button'
          className={`bg-altlighter text-altdark border-0 rounded-pill ${styles.category_pill}`}
        >
          {`#${category}`}
        </button>
      </div>

      <div className={`card-body border-0 ${styles.card_content} ${styles.card_details}`}>
        <h5 className='card-title'>
          <a className='text-reset text-decoration-none' href='#some-episode'>
            {title}
          </a>
        </h5>

        <p className='card-text'>{_.truncate(description, { length: 100 })}</p>
      </div>
    </div>
  );
};

export default EpisodeCard;
