import { Link } from 'gatsby';
import React from 'react';
import _ from 'lodash';

import * as styles from './TAIKPodcastRow.module.scss';

const TAIKPodcastRow = ({ featuredPodcast, even }) => {
  const { title, guest, image, description } = featuredPodcast;

  return (
    <>
      {even ? (
        <div className={`row gx-5 ${styles.row}`}>
          <div
            className={`col-12 col-md-5 ${styles.image}`}
            style={{ backgroundImage: `url(${image})` }}
          />
          <div className='col-12 col-md-7 py-5 d-flex flex-column justify-content-between'>
            <div>
              <h1 className={styles.title}>
                {_.truncate(title, { length: 18, omission: '' })} <br /> <span>With</span> {guest}
              </h1>
              <div dangerouslySetInnerHTML={{ __html: description }} />
            </div>

            <Link
              to='/education-found'
              className={`btn btn-primary rounded-pill align-self-end ${styles.button}`}
            >
              Listen to the podcast
            </Link>
          </div>
        </div>
      ) : (
        <div className={`row gx-5 ${styles.row}`}>
          <div className='col-12 col-md-7 py-5 d-flex flex-column justify-content-between'>
            <div>
              <h1 className={styles.title}>
                {_.truncate(title, { length: 18, omission: '' })} <br /> <span>With</span> {guest}
              </h1>
              <div dangerouslySetInnerHTML={{ __html: description }} />
            </div>

            <Link
              to='/education-found'
              className={`btn btn-primary rounded-pill align-self-start ${styles.button}`}
            >
              Listen to the podcast
            </Link>
          </div>
          <div
            className={`col-12 col-md-5 ${styles.image}`}
            style={{ backgroundImage: `url(${image})` }}
          />
        </div>
      )}
    </>
  );
};

export default TAIKPodcastRow;
