import React from 'react';
import Slider from 'react-slick';
import { Heading } from '@commons';
import './Publications.scss';
import PublicationCard from './PublicationCard';

function Publications({ articleData }) {
  // CAROUSEL STARTS

  const carouselSettings = {
    autoplay: true,
    accessibility: true,
    infinite: true,
    autoplaySpeed: 6000,
    speed: 900,
    slidesToShow: 3,
    slidesToScroll: 3,
    arrows: false,
    lazyLoad: 'progressive',
    centerMode: false,
    dots: true,
    responsive: [
      {
        breakpoint: 1366,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 1200,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 1,
          dots: true,
        },
      },
      {
        breakpoint: 992,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          dots: false,
          centerMode: false,
        },
      },
    ],
  };

  return (
    <section className='d-flex flex-column sections'>
      <div className='container' id='publicationsSlider'>
        <Heading secondary='Latest' primary='Publications' color='altdarker' />

        <Slider {...carouselSettings}>
          {articleData &&
            articleData.allMarkdownRemark.edges.map((article, index) => (
              <PublicationCard key={index} articleData={article} />
            ))}
        </Slider>
      </div>
    </section>
  );
}

export default Publications;
