import React, { useState, useRef } from 'react';
import FormData from 'form-data';
import { useFormik } from 'formik';
import { FileUploader } from 'react-drag-drop-files';
import DOMPurify from 'dompurify';
import axios from 'axios';
import './VForm.scss';
import FormButton from '../Buttons/FormButton';

const VForm = () => {
  // DECLARING STATES AND VARIABLES
  //   const [isSubmitting, setIsSubmitting] = useState(false);
  const [fileError, setFileError] = useState(null);
  const fileRef = useRef();
  const volunteerForm = useRef();
  const [file, setFile] = useState(null);
  const [generalErr, setGeneralErr] = useState(false);
  const [serverCalled, setServerCalled] = useState(false);
  const [submitSuccess, setSubmitSuccess] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [apiContactFail, setApiContactFail] = useState(false);
  const fileTypes = ['PDF', 'DOC', 'DOCX'];

  // VAR FOR SERVER SIDE INPUT VALIDATION ERROR
  const serverErr = {
    firstName: null,
    lastName: null,
    email: null,
    country: null,
    languages: null,
    biography: null,
    contribution: null,
    file: null,
    privacyPolicy: false,
  };

  // CLEAR FILE
  function clearFile() {
    fileRef.current.querySelector('input').value = null;
    setFile(null);
  }

  // HANDLE ON FOCUS
  function handleOnFocus() {
    if (submitSuccess) {
      setSubmitSuccess(false);
    }
  }
  // HANDLE FILE CHANGE
  const handleFileChange = (uploadedFile) => {
    if (apiContactFail) {
      setApiContactFail(false);
    }
    setFile(uploadedFile);
    if (serverCalled) {
      serverErr.cv = null;
    }
    // handle validations

    if (uploadedFile.size > 1024000) {
      setFileError('File size cannot exceed more than 1MB');
    }
  };

  // HANDLE INPUT CHANGE AFTER GATSBY FUNC/SERVER SIDE VALIDATION ERROR
  const handlePostServerChange = (e) => {
    const uName = e.target.name;
    if (uName !== 'privacyPolicy') {
      serverErr.uName = null;
    }
    serverErr.uName = false;
    setGeneralErr(false);
    setSubmitSuccess(false);
    if (apiContactFail) {
      setApiContactFail(false);
    }
  };

  // A custom validation function. This must return an object
  // which keys are symmetrical to our values/initialValues
  const validate = (values) => {
    const errors = {};

    if (!values.firstName) {
      errors.firstName = 'A first name is required';
    } else if (!/(^[a-zA-Z-'\s]+$)/.test(values.firstName)) {
      errors.firstName = 'Can only contain letters, spaces, apostrophes and dashes';
    }

    if (!values.lastName) {
      errors.lastName = 'A last name is required';
    } else if (!/(^[a-zA-Z-'\s]+$)/.test(values.lastName)) {
      errors.lastName = 'Can only contain letters, spaces, apostrophes and dashes';
    }

    if (!values.email) {
      errors.email = 'An Email address is required';
    } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
      errors.email = 'Invalid email address';
    }

    if (!values.country) {
      errors.country = 'A country is required';
    } else if (values.country.length > 20) {
      errors.country = 'Must be 20 characters or less';
    }

    if (!values.languages) {
      errors.languages = 'At least one language is required';
    }

    if (!values.biography) {
      errors.biography = 'Please tell us a little about you';
    }

    if (!values.contribution) {
      errors.contribution = 'Please type out how you can contribute';
    }

    if (!values.privacyPolicy) {
      errors.privacyPolicy = 'Please confirm you have read our Privacy Policy';
    }

    return errors;
  };

  // FORMIK METHOD TO HANDLE NFORM
  const formik = useFormik({
    // SETTING INITIAL FORM VALUES
    initialValues: {
      firstName: '',
      lastName: '',
      email: '',
      country: '',
      languages: '',
      biography: '',
      contribution: '',
      privacyPolicy: false,
    },

    // FORM VALIDATION
    validate,

    // SUBMIT HANDLER
    onSubmit: async (values) => {
      if (isSubmitting) {
        return;
      }
      try {
        const formData = new FormData();
        setIsSubmitting(true);
        // APPENDING TRIMMED AND SANITIZED CODE TO FORMDATA OBJECT
        formData.append('firstName', DOMPurify.sanitize(values.firstName).trim());
        formData.append('lastName', DOMPurify.sanitize(values.lastName).trim());
        formData.append('email', DOMPurify.sanitize(values.email).trim());
        formData.append('country', DOMPurify.sanitize(values.country).trim());
        formData.append('languages', DOMPurify.sanitize(values.languages).trim());
        formData.append('biography', DOMPurify.sanitize(values.biography).trim());
        formData.append('contribution', DOMPurify.sanitize(values.contribution).trim());
        formData.append('privacyPolicy', values.privacyPolicy);

        // appending file submitted to formData
        if (file) {
          if (file.size > 1024000) {
            setGeneralErr(true);
            return;
          }
          formData.append('cv', file);
          // formData.append('cv', file);
        }

        try {
          // CALLING GATSBY FUNCTION AND GETTING SERVER RESPONSE
          const url = '/api/volunteerform';

          const response = await axios.post(url, formData).then((res) => res);

          if (response) {
            setServerCalled(true);
            setIsSubmitting(false);
            if (response.data.firstNameErr) {
              serverErr.firstName = response.data.firstNameErr;
              setGeneralErr(true);
              return;
            }

            if (response.data.lastNameErr) {
              serverErr.firstName = response.data.lastNameErr;
              setGeneralErr(true);
              return;
            }

            if (response.data.emailErr) {
              serverErr.email = response.data.emailErr;
              setGeneralErr(true);
              return;
            }

            if (response.data.countryErr) {
              serverErr.country = response.data.countryErr;
              setGeneralErr(true);
              return;
            }

            if (response.data.languagesErr) {
              serverErr.languages = response.data.languagesErr;
              setGeneralErr(true);
              return;
            }

            if (response.data.biographyErr) {
              serverErr.biography = response.data.biographyErr;
              setGeneralErr(true);
              return;
            }

            if (response.data.contributionErr) {
              serverErr.contribution = response.data.contributionErr;
              setGeneralErr(true);
              return;
            }

            if (response.data.fileErr) {
              serverErr.cv = response.data.fileErr;
              setGeneralErr(true);
              return;
            }

            if (response.data.privacyPolicyErr) {
              serverErr.privacyPolicy = response.data.privacyPolicyErr;
              setGeneralErr(true);
              return;
            }

            if (response.status === 200) {
              setSubmitSuccess(true);
              formik.resetForm();
              setFile(null);
            }
          }
        } catch (error) {
          // CATCH FOR GATSBY FUNCTION API POST CALL
          console.error(error);
          setIsSubmitting(false);
          setApiContactFail(true);
        }
      } catch (err) {
        // CATCH FOR BLOCK OF CODE IN FORMIK ONSUBMIT
        console.error(err);
      }
    }, // ONSUBMIT ENDS
  }); // useFormik ENDS

  return (
    <>
      <div className='container-fluid form-wrapper' id='v-form-wrapper'>
        <div className='row form-holder'>
          <div className='col-12 v-form'>
            <form
              id='volunteerForm'
              name='volunteerForm'
              onSubmit={formik.handleSubmit}
              encType='multipart/form-data'
              className='d-flex flex-column align-items-center justify-content-between mt-1 mb-5'
              ref={volunteerForm}
            >
              {/* FIRST AND LAST HOLDER */}
              <div className='row p-0 w-100 justify-content-sm-between'>
                <div className='first-name-group col-12 col-lg-6'>
                  <div className='form-group d-flex flex-column p-0 w-100'>
                    <label
                      className='mainLabel d-flex justify-content-start align-items-end mb-2'
                      htmlFor='firstName'
                    >
                      <h6 className='me-1'>First Name</h6>
                    </label>
                    <input
                      id='firstName'
                      name='firstName'
                      type='text'
                      onChange={function (e) {
                        if (serverCalled) {
                          handlePostServerChange(e);
                        }
                        formik.handleChange(e);
                        if (apiContactFail) {
                          setApiContactFail(false);
                        }
                      }}
                      onFocus={handleOnFocus}
                      value={formik.values.firstName}
                      className='form-control'
                      placeholder='Type your first name'
                    />
                  </div>
                  {serverErr.firstName ? (
                    <div className='volunteer-form-error'>{serverErr.firstName}</div>
                  ) : null}

                  {formik.errors.firstName ? (
                    <div className='volunteer-form-error'>{formik.errors.firstName}</div>
                  ) : null}
                </div>

                <div className='last-name-group col-12 col-lg-6'>
                  {/* last name */}
                  <div className='form-group mt-4 mt-sm-0 d-flex flex-column p-0 w-100'>
                    <label
                      className='mainLabel d-flex justify-content-start align-items-end mb-2'
                      htmlFor='lastName'
                    >
                      <h6 className='me-1'>Last Name</h6>
                    </label>
                    <input
                      id='lastName'
                      name='lastName'
                      type='text'
                      onChange={(e) => {
                        if (serverCalled) {
                          handlePostServerChange(e);
                        }
                        formik.handleChange(e);
                        if (apiContactFail) {
                          setApiContactFail(false);
                        }
                      }}
                      onFocus={handleOnFocus}
                      value={formik.values.lastName}
                      className='form-control'
                      placeholder='Type your last name'
                    />
                  </div>
                  {serverErr.lastName ? (
                    <div className='volunteer-form-error'>{serverErr.lastName}</div>
                  ) : null}
                  {formik.errors.lastName ? (
                    <div className='volunteer-form-error'>{formik.errors.lastName}</div>
                  ) : null}
                </div>
              </div>

              {/* email */}
              <div className='form-group mt-4 d-flex flex-column'>
                <label
                  className='mainLabel d-flex justify-content-start align-items-end mb-2'
                  htmlFor='email'
                >
                  <h6 className='me-1'>Email</h6>
                </label>
                <input
                  id='email'
                  name='email'
                  type='email'
                  onChange={(e) => {
                    if (serverCalled) {
                      handlePostServerChange(e);
                    }
                    formik.handleChange(e);
                    if (apiContactFail) {
                      setApiContactFail(false);
                    }
                  }}
                  onFocus={handleOnFocus}
                  value={formik.values.email}
                  className='form-control'
                  placeholder='Type your email address'
                />
              </div>
              {serverErr.email ? (
                <div className='volunteer-form-error'>{serverErr.email}</div>
              ) : null}
              {formik.errors.email ? (
                <div className='volunteer-form-error'>{formik.errors.email}</div>
              ) : null}
              {/* country */}
              <div className='form-group mt-4 d-flex flex-column'>
                <label
                  className='mainLabel d-flex justify-content-start align-items-center mb-2'
                  htmlFor='country'
                >
                  <h6 className='me-1'>Country of origin</h6>
                </label>
                <input
                  id='country'
                  name='country'
                  type='text'
                  onChange={(e) => {
                    if (serverCalled) {
                      handlePostServerChange(e);
                    }
                    formik.handleChange(e);
                    if (apiContactFail) {
                      setApiContactFail(false);
                    }
                  }}
                  onFocus={handleOnFocus}
                  value={formik.values.country}
                  className='form-control'
                  placeholder='Type your country of origin'
                />
              </div>
              {serverErr.country ? (
                <div className='volunteer-form-error'>{serverErr.country}</div>
              ) : null}
              {formik.errors.country ? (
                <div className='volunteer-form-error'>{formik.errors.country}</div>
              ) : null}
              {/* languages */}

              <div className='form-group mt-4 d-flex flex-column'>
                <label
                  className='mainLabel d-flex justify-content-start align-items-end mb-2'
                  htmlFor='languages'
                >
                  <h6 className='me-1'>Languages you speak fluently</h6>
                </label>
                <input
                  id='languages'
                  name='languages'
                  type='text'
                  onChange={(e) => {
                    if (serverCalled) {
                      handlePostServerChange(e);
                    }
                    formik.handleChange(e);
                    if (apiContactFail) {
                      setApiContactFail(false);
                    }
                  }}
                  onFocus={handleOnFocus}
                  value={formik.values.languages}
                  className='form-control'
                  placeholder='Ex: Kiswahili, Arabic, Portuguese, etc.'
                />
              </div>
              {serverErr.languages ? (
                <div className='volunteer-form-error'>{serverErr.languages}</div>
              ) : null}
              {formik.errors.languages ? (
                <div className='volunteer-form-error'>{formik.errors.languages}</div>
              ) : null}

              {/* biography */}
              <div className='form-group mt-4 d-flex flex-column'>
                <label
                  className='mainLabel d-flex justify-content-start align-items-end mb-2'
                  htmlFor='biography'
                >
                  <h6 className='me-1'>A little about you </h6>
                </label>
                <textarea
                  id='biography'
                  name='biography'
                  type='text'
                  onChange={(e) => {
                    if (serverCalled) {
                      handlePostServerChange(e);
                    }
                    formik.handleChange(e);
                    if (apiContactFail) {
                      setApiContactFail(false);
                    }
                  }}
                  onFocus={handleOnFocus}
                  value={formik.values.biography}
                  className='form-control'
                  placeholder='Tell us a little about yourself'
                />
              </div>
              {serverErr.biography ? (
                <div className='volunteer-form-error'>{serverErr.biography}</div>
              ) : null}
              {formik.errors.biography ? (
                <div className='volunteer-form-error'>{formik.errors.biography}</div>
              ) : null}

              {/* contribution */}
              <div className='form-group mt-4 d-flex flex-column'>
                <label
                  className='mainLabel d-flex justify-content-start align-items-end mb-2'
                  htmlFor='contribution'
                >
                  <h6 className='me-1'>How do you want to contribute?</h6>
                </label>
                <textarea
                  id='contribution'
                  name='contribution'
                  type='text'
                  onChange={(e) => {
                    if (serverCalled) {
                      handlePostServerChange(e);
                    }
                    formik.handleChange(e);
                    if (apiContactFail) {
                      setApiContactFail(false);
                    }
                  }}
                  onFocus={handleOnFocus}
                  value={formik.values.contribution}
                  className='form-control'
                  placeholder='Tell us how you want to contribute your time'
                />
              </div>
              {serverErr.contribution ? (
                <div className='volunteer-form-error'>{serverErr.contribution}</div>
              ) : null}
              {formik.errors.contribution ? (
                <div className='volunteer-form-error'>{formik.errors.contribution}</div>
              ) : null}

              {/* cv upload */}
              <div className='form-group d-flex flex-column mt-4' ref={fileRef}>
                <label
                  className='mainLabel d-flex justify-content-start align-items-end mb-2'
                  htmlFor='cv'
                >
                  <h6 className='me-1'>Upload your CV</h6>

                  <br />
                  <div className='clear-file mt-1'>
                    <span title='Click to Clear File' onClick={clearFile}>
                      Clear File
                    </span>
                  </div>
                </label>

                <FileUploader
                  id='cv'
                  name='cv'
                  handleChange={handleFileChange}
                  types={fileTypes}
                  classes='d-block'
                >
                  <div className='file flex-column flex-sm-row'>
                    <button
                      type='button'
                      className='btn btn-altdark text-uppercase rounded-pill button'
                    >
                      choose file
                    </button>
                    <p>
                      {file
                        ? `${file.name}`
                        : 'Or drag and drop the file here. (.PDF, .DOC or .DOCX only)'}
                    </p>
                  </div>
                </FileUploader>
              </div>
              {serverErr.cv ? <div className='volunteer-form-error'>{serverErr.cv}</div> : null}
              {fileError ? <div className='volunteer-form-error'>{fileError}</div> : null}

              {/* PRIVACY POLICY CHECKBOX */}
              <div className='form-group consent w-100 mt-3'>
                <input
                  type='checkbox'
                  onChange={(e) => {
                    if (serverCalled) {
                      handlePostServerChange(e);
                    }
                    formik.handleChange(e);
                    if (apiContactFail) {
                      setApiContactFail(false);
                    }
                  }}
                  name='privacyPolicy'
                  id='privacyPolicy'
                  checked={formik.values.privacyPolicy}
                  required
                />
                <span>
                  I have read and understood The Africa I Know&nbsp;
                  <a href='/privacypolicy' target='_blank'>
                    <span className='policy-link'>Privacy Policy</span>
                  </a>
                </span>
              </div>
              {serverErr.privacyPolicy ? (
                <div className='volunteer-form-error'>{serverErr.privacyPolicy}</div>
              ) : null}
              {formik.errors.privacyPolicy ? (
                <div className='volunteer-form-error'>{formik.errors.privacyPolicy}</div>
              ) : null}

              {/* SUBMIT BUTTON */}
              <div className='d-flex justify-content-end align-items-center mt-3 align-self-end me-2'>
                <FormButton text='Submit' />
              </div>
              {/* Errors and Notices */}
              {isSubmitting && (
                <div className='is-submitting mt-2 me-2'>
                  <span>...</span>&nbsp;Submitting. Thank you for your patience.{' '}
                </div>
              )}
              <div className={`${generalErr ? 'stateShow' : ''} generalError mt-3 me-2`}>
                <span>Submission issues</span> <br />{' '}
                <div>Please fix the errors in the form before submitting</div>
              </div>
              {submitSuccess && (
                <div
                  className='submitSuccess mt-4 me-2 d-flex flex-column align-items-start justify-content-center pb-3 pt-3 w-100'
                  style={{ maxWidth: '65ch' }}
                >
                  <h6 className='mb-2' style={{ width: '100%', textAlign: 'left' }}>
                    Message submitted!
                  </h6>
                  <span>
                    Thank you for submitting your application. We will be in touch shortly.
                  </span>
                </div>
              )}

              {apiContactFail && (
                <div className='d-flex py-2 flex-column justify-content-center align-items-start generalError mt-3 me-2'>
                  <span>Connection issues</span>
                  <div>
                    We could not connect with our backend. Please check internet connection and try
                    again in a minute.
                  </div>
                </div>
              )}
            </form>
          </div>
        </div>
      </div>
    </>
  );
};

export default VForm;
