import React, { useContext } from 'react';
import Helmet from 'react-helmet';
import { FaPlay } from 'react-icons/fa';
import { GeneralContext } from '../../context/GeneralContext';
import './HomeBanner.scss';

import * as styles from './Home.module.scss';

const HomeBanner = () => {
  const modalValue = useContext(GeneralContext);

  const playHomeVideo = () => {
    modalValue.setHomeVideoModal(true);
  };
  return (
    <div className='d-flex flex-column justify-content-center align-items-center w-100 h-100'>
      <Helmet>
        <body className={modalValue.homeVideoModal ? 'body-noscroll' : null} />
      </Helmet>
      <h1 className='africa-font text-center home-title'>
        Our mission is to positively change the narrative about Africa
      </h1>

      <button
        type='button'
        className={styles.play_btn}
        onClick={playHomeVideo}
        aria-label='Opens popup video about company at start of homepage'
      >
        <span>
          <FaPlay />
        </span>
      </button>
    </div>
  );
};

export default HomeBanner;
