import { Link } from 'gatsby';
import React from 'react';
import {
  FaEnvelope,
  FaFacebookF,
  FaLinkedinIn,
  FaQuoteLeft,
  FaTwitter,
  FaWhatsapp,
} from 'react-icons/fa';
import * as styles from './index.module.scss';

const SingleFeaturedDev = ({ articleData }) => {
  const { content, aboutAuthor, personImageOne, personImageTwo, quote, qna } = articleData;
  const socials = [
    { path: '#', icon: <FaTwitter size={27} /> },
    { path: '#', icon: <FaFacebookF size={27} /> },
    { path: '#', icon: <FaLinkedinIn size={27} /> },
    { path: '#', icon: <FaWhatsapp size={27} /> },
    { path: '#', icon: <FaEnvelope size={27} /> },
  ];

  return (
    <>
      <section className='bg-altlightest w-100'>
        <div
          className={`row ${styles.featured_photo}`}
          style={{ backgroundImage: `url(${personImageOne})` }}
        />

        <div className={`${styles.article_container}`}>
          <div
            className={`row ${styles.article_text}`}
            dangerouslySetInnerHTML={{ __html: content }}
          />
        </div>

        <div
          className={`row ${styles.featured_photo}`}
          style={{ backgroundImage: `url(${personImageTwo})` }}
        />

        <div className={`${styles.article_container_large}`}>
          <p className={`${styles.quote}`}>
            <FaQuoteLeft className={`${styles.quote_icon}`} size={51} />
            {quote}
          </p>

          <div className={`${styles.qna}`} dangerouslySetInnerHTML={{ __html: qna }} />
        </div>

        <div className={`${styles.article_container}`}>
          <div className={`${styles.about_author}`}>
            <h2>About the author</h2>

            <div dangerouslySetInnerHTML={{ __html: aboutAuthor }} />
          </div>

          <p className={`${styles.contact_us}`}>
            Know someone we should feature? Email us at{' '}
            <a href='mailto:editors@theafricaiknow.org'>editors@theafricaiknow.org</a>
          </p>

          <div className={`${styles.share}`}>
            <h2>Share this article</h2>
            <div className='d-flex justify-content-center'>
              {socials.map(({ path, icon }) => (
                <Link
                  className={`me-3 ${styles.social}`}
                  to={path}
                  style={{
                    '--social-button-color': `var(--color-white)`,
                    '--social-button-color-hover': `var(--color-dark-200)`,
                    '--social-button-size': `52px`,
                    '--social-button-margin': `18px`,
                  }}
                >
                  {icon}
                </Link>
              ))}
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default SingleFeaturedDev;
