import React, { useState, useRef } from 'react';
import DOMPurify from 'dompurify';
import CurrencyFormat from 'react-currency-format';
import axios from 'axios';
import { Heading } from '@commons';
import * as styles from './Donate.module.scss';

const checkWindow = typeof window !== 'undefined';

export default function Donate() {
  const amounts = [
    {
      amount: '$20',
      valueNum: 20,
    },
    {
      amount: '$40',
      valueNum: 40,
    },
    {
      amount: '$60',
      valueNum: 60,
    },
    {
      amount: '$80',
      valueNum: 80,
    },
    {
      amount: '$100',
      valueNum: 100,
    },
    {
      amount: 'Other',
      valueNum: null,
    },
    {
      amount: 'Other',
      valueNum: 'Input',
    },
  ];

  const [processing, setProcessing] = useState(false);
  const [organization, setOrganization] = useState(null);
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [donationErr, setDonationErr] = useState(null);
  const [currencyErr, setCurrencyErr] = useState(null);
  const [nameErr, setNameErr] = useState(null);
  const [emailErr, setEmailErr] = useState(null);
  const [donationAmount, setDonationAmount] = useState(0);
  const [formattedView, setFormattedView] = useState();

  const donateForm = useRef();
  const checkboxHolder = useRef();

  let response;
  // CALL TO SINGLE PAYMENT CHECKOUT API
  async function sessionPayment(e) {
    e.preventDefault();

    // CLIENT SIDE CHECKS

    if (!donationAmount || donationAmount.trim() === '') {
      setDonationErr('Sorry, the minimum donation amount is $3.');
      donateForm.current.querySelector('input#amountInput').focus();
      return;
    }

    if (Number.isNaN(donationAmount)) {
      setDonationErr('Sorry, incorrect format. The donation amount must be a number.');
      donateForm.current.querySelector('input#amountInput').focus();
      return;
    }

    if (donationAmount < 3) {
      setDonationErr('Sorry, the minimum donation amount is $3.');
      donateForm.current.querySelector('input#amountInput').focus();
      return;
    }

    if (!name || name.trim() === '') {
      setNameErr('Sorry, a name is required to proceed.');
      donateForm.current.querySelector('input[name="name"]').focus();
      return;
    }
    if (!/(^[a-zA-Z-'\s]+$)/.test(name)) {
      setNameErr('The Name Field can only contain letters, spaces, apostrophes and dashes.');
      donateForm.current.querySelector('input[name="name"]').focus();
      return;
    }

    if (!email || email.trim() === '') {
      setEmailErr('Sorry, an email address is required to proceed.');
      donateForm.current.querySelector('input[name="email"]').focus();
      return;
    }

    if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email)) {
      setEmailErr('Invalid email address.');
      donateForm.current.querySelector('input[name="email"]').focus();
      return;
    }

    let paymentPayload;
    if (!organization) {
      paymentPayload = {
        amount: donationAmount,
        name: DOMPurify.sanitize(name.trim()),
        email: DOMPurify.sanitize(email.trim()),
      };
    }

    if (organization) {
      paymentPayload = {
        amount: donationAmount,
        name: DOMPurify.sanitize(name.trim()),
        email: DOMPurify.sanitize(email.trim()),
        organization: DOMPurify.sanitize(organization.trim()),
      };
    }

    try {
      setProcessing(true);

      response = await axios
        .post('/api/create-checkout-session-payment', paymentPayload)
        .then((res) => res);

      if (response.status === 200) {
        // console.log(response.data.Message);
        console.log(response);

        // console.log(response.data.Checkout_Link);
        setProcessing(false);
        if (checkWindow) {
          // eslint-disable-next-line no-undef
          window.location = response.data.Checkout_Link;
        }
        return;
      }

      if (response.status === 440) {
        if (response.data.nameErr) {
          console.error(response.data.nameErr);
          setNameErr(response.data.nameErr);
          donateForm.current.querySelector('input.nameInput').focus();
          return;
        }
        if (response.data.emailErr) {
          console.error(response.data.emailErr);
          setEmailErr(response.data.emailErr);
          donateForm.current.querySelector('input.emailInput').focus();

          return;
        }
        if (response.data.amountError) {
          console.error(response.data.amountError);
          setDonationErr(response.data.amountError);
          donateForm.current.querySelector('input#amountInput').focus();
          return;
        }
      }

      if (response.status === 500) {
        console.error(response.data.Error);
        setCurrencyErr('Something went wrong on our end. Please try again in a minute.');
        donateForm.current.querySelector('input[name="name"]').focus();
        return;
      }

      // CONNECTS SUCCESSFULLY BUT CHECKS IF DATA VALIDATION ON SERVER FAILED
      if (response.data.Error) {
        console.error(response.data.Error);
        setCurrencyErr(
          'Something went wrong with the attempted donation. Please fix the errors and try again.',
        );
        setProcessing(false);
        donateForm.current.querySelector('input#amountInput').focus();
      }
    } catch (err) {
      console.error(err);
      setCurrencyErr(
        'Apologies. Something went wrong with the attempted donation. Please try again in a minute.',
      );
      setProcessing(false);
    }
  }

  // Handle form submit
  function donationSubmit(e) {
    e.preventDefault();
  }

  // ON CHANGE

  function handleNameChange(e) {
    setName(e.target.value);
    setNameErr(null);
    setCurrencyErr(null);
  }
  function handleEmailChange(e) {
    setEmail(e.target.value);
    setEmailErr(null);
    setCurrencyErr(null);
  }
  function handleOrganizationChange(e) {
    setOrganization(e.target.value);
    setCurrencyErr(null);
  }

  // FUNCTIONS FOR HANDLING DONATION AMOUNTS

  const handleRadioButtonChange = (e) => {
    if (e.target !== checkboxHolder.current.querySelectorAll('input')[5]) {
      checkboxHolder.current.querySelectorAll('input')[6].setAttribute('disabled', '');
      setDonationAmount(e.target.value);
      if (formattedView) {
        setFormattedView('');
      }
      if (donationErr) {
        setDonationErr(null);
      }
      if (currencyErr) {
        setCurrencyErr(null);
      }
      return;
    }

    if (e.target === checkboxHolder.current.querySelectorAll('input')[5]) {
      const otherAmountInput = checkboxHolder.current.querySelectorAll('input')[6];
      otherAmountInput.removeAttribute('disabled');
      otherAmountInput.focus();
      if (donationAmount !== 0) {
        setDonationAmount(0);
      }
      if (donationErr) {
        setDonationErr(null);
      }
      if (currencyErr) {
        setCurrencyErr(null);
      }
    }
  };

  return (
    <>
      <section className={`${styles.donate_section} w-100 h-auto position-relative mb-0`}>
        <div className={`${styles.donate_container} container-fluid`}>
          <div className='row d-flex justify-content-center align-items-start'>
            <div
              className={`${styles.donate_box} position-relative mb-2 d-flex flex-column justify-content-start align-items-center`}
            >
              <Heading secondary='Make a' primary='Donation' color='altdarker' center />
              <p className={`${styles.header_paragraph} text-center text-altdark mb-5`}>
                Every donation no matter how big or small, makes a significant difference to our
                cause.
                <br /> Thank you for doing your part in helping to positively change the narrative
                about Africa.
              </p>
              <form
                className={`${styles.donationForm} w-100 mx-auto d-flex flex-column justify-content-center align-items-center mt-3`}
                name='donationForm'
                id='donationForm'
                encType='application/json'
                ref={donateForm}
                onSubmit={donationSubmit}
              >
                {/* SHOWS TOTAL */}

                <div
                  className={`${styles.input_holder} w-100 mb-3 mx-auto d-flex justify-content-start align-items-center flex-column p-0`}
                >
                  <div className='row p-0 w-100 justify-content-sm-between'>
                    <div className='col-12 col-lg-6 p-0 pe-lg-2'>
                      <div className='form-group d-flex flex-column p-0 w-100'>
                        <label
                          className='mainLabel d-flex justify-content-start align-items-end mb-1'
                          htmlFor='name'
                        >
                          <h6 className='me-1'>Name</h6>
                        </label>
                        <input
                          id='name'
                          name='name'
                          type='text'
                          onChange={function (e) {
                            handleNameChange(e);
                          }}
                          value={name}
                          className='form-control nameInput'
                          placeholder='Type your name'
                          required
                        />
                        {nameErr && <div className={`${styles.err_message}`}>{nameErr}</div>}
                      </div>
                      {/* form group ends */}
                    </div>

                    <div className='col-12 col-lg-6 p-0 ps-lg-2 mt-4 mt-lg-0'>
                      {/* email */}
                      <div className='form-group mt-4 mt-md-0 d-flex flex-column p-0 w-100'>
                        <label
                          className='mainLabel d-flex justify-content-start align-items-end mb-1'
                          htmlFor='email'
                        >
                          <h6 className='me-1'>Email</h6>
                        </label>
                        <input
                          id='email'
                          name='email'
                          type='email'
                          onChange={function (e) {
                            handleEmailChange(e);
                          }}
                          value={email}
                          className='form-control emailInput'
                          placeholder='Type your email'
                          required
                        />
                        {emailErr && <div className={styles.err_message}>{emailErr}</div>}
                      </div>
                      {/* form group ends */}
                    </div>
                  </div>

                  <div className='form-group mt-4 d-flex flex-column p-0 w-100 donation-block'>
                    <label
                      className='mainLabel d-flex justify-content-start align-items-end mt-4 mb-0'
                      htmlFor='amount'
                    >
                      <h6 className='me-1 mb-1'>Donation Amount</h6>
                    </label>

                    <div
                      className={`${styles.donation_row} d-flex justify-content-start align-items-between flex-row flex-wrap`}
                      ref={checkboxHolder}
                    >
                      <div className={`${styles.checkbox_holder} position-relative`}>
                        {amounts.map(({ amount, valueNum }, index) => {
                          if (index !== 6) {
                            return (
                              <label
                                htmlFor={`custom-checkbox-${index}`}
                                className={`${styles.radio_label} me-4 mt-2`}
                              >
                                {amount}
                                <input
                                  type='radio'
                                  id={`custom-checkbox-${index}`}
                                  name='amountRadio'
                                  value={valueNum}
                                  onChange={(e) => handleRadioButtonChange(e)}
                                />
                                <span className={styles.checkmark} />
                              </label>
                            );
                          }

                          return (
                            <CurrencyFormat
                              id='amountInput'
                              placeholder='Amount'
                              className='mt-2'
                              value={formattedView}
                              thousandSeparator
                              prefix='$'
                              allowNegative={false}
                              decimalScale={2}
                              fixedDecimalScale
                              disabled
                              onValueChange={(values) => {
                                const { formattedValue, value } = values;
                                // formattedValue = $2,223
                                // value ie, 2223
                                setDonationAmount(value);
                                setFormattedView(formattedValue);
                                setDonationErr(null);
                                setCurrencyErr(null);
                              }}
                            />
                          );
                        })}
                      </div>
                      {/* <CurrencyFormat
                        id='amountInput'
                        placeholder='Amount'
                        className='mt-3'
                        value={formattedView}
                        thousandSeparator
                        prefix='$'
                        allowNegative={false}
                        decimalScale={2}
                        fixedDecimalScale
                        disabled
                        onValueChange={(values) => {
                          const { formattedValue, value } = values;
                          // formattedValue = $2,223
                          // value ie, 2223
                          setDonationAmount(value);
                          setFormattedView(formattedValue);
                          setDonationErr(null);
                          setCurrencyErr(null);
                        }}
                      /> */}
                    </div>

                    {donationErr && <div className={`${styles.err_message}`}>{donationErr}</div>}
                  </div>
                  {/* form group ends */}

                  <div className='form-group mt-4 d-flex flex-column p-0 w-100 company-block'>
                    <label
                      className='mainLabel d-flex justify-content-start align-items-end mb-1'
                      htmlFor='organization'
                    >
                      <h6 className='me-1'>Company name (if applicable)</h6>
                    </label>
                    <input
                      id='organization'
                      name='organization'
                      type='text'
                      onChange={function (e) {
                        handleOrganizationChange(e);
                      }}
                      value={organization}
                      className='form-control organizationInput'
                      placeholder="Type your company's name"
                    />
                  </div>
                  {/* form group ends */}
                </div>
                <div
                  className={`${styles.total_holder} mt-5 me-1 ms-auto d-flex flex-nowrap align-items-center justify-content-end px-3 py-2 bg-altsuperlight`}
                >
                  <h4 className='me-4 mb-0'>TOTAL:</h4>
                  <h4 className='mb-0 bg-altsuperlight ms-2' style={{ borderRadius: '6px' }}>
                    ${donationAmount}
                  </h4>
                </div>

                {/* FINALIZE DONATION */}

                <div
                  className={`${styles.btns_holder} w-100 position-relative mt-4 d-flex justify-content-end flex-nowrap flex-row align-items-stretch`}
                >
                  <div className='d-flex flex-column align-items-center justify-content-center p-0 w-100'>
                    <button
                      type='button'
                      className={`${styles.donate_button} w-100 mb-3 mx-auto flex-grow-1 d-flex flex-row justify-content-end align-items-center rounded-pill btn btn-primary`}
                      id='donateCompleteBtn'
                      onClick={sessionPayment}
                      aria-label='Takes you to checkout page for monthly recurring donations'
                    >
                      <span className='my-auto mx-auto'>CONTINUE </span>
                      <i className={`bi bi-arrow-right-short my-auto ${styles.btn_icon}`} />
                    </button>
                  </div>
                </div>
                {processing && (
                  <div className={styles.processing}>
                    <h6>Processing...</h6>
                  </div>
                )}
                {currencyErr && <div className={styles.err_message}>{currencyErr}</div>}
              </form>
            </div>
          </div>
          {/* row ends */}
        </div>
        {/* containder-fluid ends */}
      </section>
    </>
  );
}
