import React, { useEffect, useState } from 'react';
import { SubHeading } from '@commons';
import { Link } from 'gatsby';

import PeopleCard from './PeopleCard';
import * as styles from './index.module.scss';

const People = ({ data }) => {
  const [adminVolunteers, setAdminVolunteers] = useState([]);
  const [educationVolunteers, setEducationVolunteers] = useState([]);
  const [mediaVolunteers, setMediaVolunteers] = useState([]);
  const [designTechVolunteers, setDesignTechVolunteers] = useState([]);
  const [translateVolunteers, setTranslateVolunteers] = useState([]);

  async function filterVolunteers(team) {
    const mdVolunteers = await data.allMarkdownRemark.edges.filter(
      ({ node }) => node.frontmatter.team === team,
    );
    return mdVolunteers;
  }

  useEffect(async () => {
    // General => general
    const finalAdminVolunteers = await filterVolunteers('general');
    if (finalAdminVolunteers) {
      setAdminVolunteers(finalAdminVolunteers);
    }

    // Education => education
    const finalEducationVolunteers = await filterVolunteers('education');
    if (finalEducationVolunteers) {
      setEducationVolunteers(finalEducationVolunteers);
    }

    // Media => media
    const finalMediaVolunteers = await filterVolunteers('media');
    if (finalMediaVolunteers) {
      setMediaVolunteers(finalMediaVolunteers);
    }

    // Design & Tech = vertical
    const finalDesignVolunteers = await filterVolunteers('vertical');
    if (finalDesignVolunteers) {
      setDesignTechVolunteers(finalDesignVolunteers);
    }

    // Translation = translation
    const finalTranslationVolunteers = await filterVolunteers('translation');
    if (finalTranslationVolunteers) {
      setTranslateVolunteers(finalTranslationVolunteers);
    }
  }, []);

  console.log(
    adminVolunteers,
    educationVolunteers,
    mediaVolunteers,
    designTechVolunteers,
    translateVolunteers,
  );
  return (
    <section className='sections'>
      {/* General */}
      <div className='container mb-5'>
        <div className='row gx-4 gy-5'>
          {adminVolunteers.map((volunteer) => (
            <div key={volunteer.node.id} className='col-12 col-md-6 col-lg-3 col-xl-2'>
              <PeopleCard volunteer={volunteer.node.frontmatter} />
            </div>
          ))}
        </div>
      </div>

      {/* Education */}
      <div className='container mb-5'>
        <div className='row'>
          <SubHeading primary='Education' secondary='Team' color='altdarker' />
        </div>

        <div className='row gx-4 gy-5'>
          {educationVolunteers.map((volunteer) => (
            <div key={volunteer.node.id} className='col-12 col-md-6 col-lg-3 col-xl-2'>
              <PeopleCard volunteer={volunteer.node.frontmatter} />
            </div>
          ))}
        </div>
      </div>

      {/* Media */}
      <div className='container mb-5'>
        <div className='row'>
          <SubHeading primary='Media' secondary='Team' color='altdarker' />
        </div>

        <div className='row gx-4 gy-5'>
          {mediaVolunteers.map((volunteer) => (
            <div key={volunteer.node.id} className='col-12 col-md-6 col-lg-3 col-xl-2'>
              <PeopleCard volunteer={volunteer.node.frontmatter} />
            </div>
          ))}
        </div>
      </div>

      {/* Design & Tech */}
      <div className='container mb-5'>
        <div className='row'>
          <SubHeading primary='Design & Tech' secondary='Team' color='altdarker' />
        </div>

        <div className='row gx-4 gy-5'>
          {designTechVolunteers.map((volunteer) => (
            <div key={volunteer.node.id} className='col-12 col-md-6 col-lg-3 col-xl-2'>
              <PeopleCard volunteer={volunteer.node.frontmatter} />
            </div>
          ))}
        </div>
      </div>

      {/* Translation */}
      <div className='container mb-5'>
        <div className='row'>
          <SubHeading primary='Translation' secondary='' color='altdarker' />
        </div>

        <div className='row gx-4 gy-5'>
          {translateVolunteers.map((volunteer) => (
            <div key={volunteer.node.id} className='col-12 col-md-6 col-lg-3 col-xl-2'>
              <PeopleCard volunteer={volunteer.node.frontmatter} />
            </div>
          ))}
        </div>
      </div>

      <div className='container mb-5'>
        <div className='row'>
          <SubHeading primary='Ambassadors' color='altdarker' />
        </div>
        <div className='col'>
          <Link
            to='https://forms.gle/1rM6q1uJYvmucGqKA'
            target='_blank'
            className={`btn btn-primary rounded-pill ${styles.button}`}
          >
            Apply to represent TAIK in your country as an Ambassador
            <i className={`bi bi-arrow-right-short  ${styles.btn_icon} ms-2`} />
          </Link>
        </div>
      </div>
    </section>
  );
};

export default People;
