import { FaPlayCircle } from 'react-icons/fa';
import React, { useEffect, useState } from 'react';
import faker from 'faker';
import { Heading } from '@commons';
import * as styles from './index.module.scss';
import TAIK54Card from './TAIK54Card';
import TAIK54PopUp from './TAIK54PopUp';

const TAIK54 = () => {
  const [inspiringPeople, setInspiringPeople] = useState([]);
  const [isPopupVisible, setIsPopupVisible] = useState(false);
  const [selectedInspiringPerson, setSelectedInspiringPerson] = useState({
    description: '',
    quote: '',
  });

  const showPopup = (person) => {
    setSelectedInspiringPerson(person);
    setIsPopupVisible(true);
  };

  const closePopup = () => {
    setIsPopupVisible(false);
  };

  useEffect(() => {
    const fetchArticles = async () => {
      try {
        const people = [];

        for (let index = 0; index < 10; index++) {
          const country = faker.random.arrayElement([
            'Mozambique',
            'Angola',
            'Kenya',
            'South Africa',
          ]);
          const countryFlag = faker.random.arrayElement([
            'https://www.countryflags.com/flag-of-mozambique/',
            'https://cdn.countryflags.com/thumbs/angola/flag-square-250.png',
            'https://en.wikipedia.org/wiki/File:Flag_of_Mozambique.svg',
            'https://cdn.countryflags.com/thumbs/south-africa/flag-square-250.png',
            'https://cdn.countryflags.com/thumbs/kenya/flag-square-250.png',
          ]);
          const name = faker.name.findName();
          const image = faker.image.imageUrl();
          const description = faker.lorem.paragraph();
          const quote = faker.lorem.paragraph(15);

          people.push({ country, countryFlag, name, image, description, quote });
        }

        setInspiringPeople(people);
      } catch (error) {
        console.error(error);
      }
    };

    fetchArticles();
  }, []);

  return (
    <>
      <section className='sections bg-altsuperlight'>
        <div className='container'>
          <div className='row'>
            <Heading secondary='About' primary='TAIK 54' color='altdarker' />
          </div>

          <div className='row'>
            <p>
              Lorem ipsum dolor sit amet consectetur, adipisicing elit. Error, accusantium quos
              soluta voluptatem aliquam maiores eaque. Itaque, repellat. Odio assumenda architecto
              accusamus modi quas?
            </p>
            <p>
              Error, accusantium quos soluta voluptatem aliquam maiores eaque. Itaque, repellat.
              Odio assumenda architecto accusamus modi quas? Lorem ipsum dolor sit amet consectetur,
              adipisicing elit. Error, accusantium quos soluta voluptatem aliquam maiores eaque.
              Itaque, repellat. Odio assumenda architecto accusamus modi quas?
            </p>
            <p>
              Lorem ipsum dolor sit amet consectetur, adipisicing elit. Error, accusantium quos
              soluta voluptatem aliquam maiores eaque. Itaque, repellat. Odio assumenda architecto
              accusamus modi quas?
            </p>
            <p>
              Lorem ipsum dolor sit amet consectetur, adipisicing elit. Error, accusantium quos
              soluta voluptatem aliquam maiores eaque. Itaque, repellat. Odio assumenda architecto
              accusamus modi quas?
            </p>
          </div>
          <div>
            <button className={styles.button_watch} type='button'>
              Watch the 2021 winners video <FaPlayCircle className={styles.icon} size={24} />
            </button>
          </div>
        </div>
      </section>

      <section className='sections bg-altlightest'>
        <div className='container'>
          <div className='row'>
            <Heading
              secondary='TAIK 54 Most Inspiring Africans'
              primary='• 2021'
              color='altdarker'
            />
          </div>

          <div className='row gx-4 gy-5'>
            {inspiringPeople &&
              inspiringPeople.map((inspiringPerson, index) => (
                <div className='col-12 col-md-6 col-lg-3'>
                  <TAIK54Card key={index} inspiringPerson={inspiringPerson} showPopup={showPopup} />
                </div>
              ))}
          </div>
        </div>
      </section>
      <TAIK54PopUp
        selectedInspiringPerson={selectedInspiringPerson}
        isPopupVisible={isPopupVisible}
        closePopup={closePopup}
      />
    </>
  );
};

export default TAIK54;
