import React from 'react';

import banner from '../../assets/images/bgImage.png';

import * as styles from './Join.module.scss';

const JoinUs = () => (
  <div className='container'>
    <div
      className={`d-flex flex-column row flex-md-row justify-content-between align-items-center ${styles.wrapper}`}
    >
      <div
        className={`mb-5 mb-xl-0 mx-auto mx-xl-0 col-12 col-xl-7 d-flex flex-column pe-0 ps-0 ${styles.text_container}`}
      >
        <div className='africa-font home-title text-start'>Join our growing team of volunteers</div>

        <p className={`m-0 ${styles.description}`}>
          Lorem ipsum dolor sit amet, consectetur adipiscing elit ut aliquam, purus sit amet luctus
          venenatis, lectus magna fringilla urna, porttitor rhoncus dolor purus non, lectus magna
          fringilla urna, porttitor rhoncus dolor purus non., lectus magna fringilla urna, porttitor
          rhoncus dolor purus non, porttitor rhoncus dolor purus.
        </p>

        <div
          className={`border-3 border-top border-white d-flex justify-content-between align-items-start justify-content-sm-center row mx-0 w-100 ${styles.numbers}`}
        >
          <div className='d-flex flex-column col-6 col-sm-4'>
            <span className='fw-bold'>40+ Volunteers</span>
            <span className='small'>Already joined our team</span>
          </div>

          <div className='d-flex flex-column col-6 col-sm-4'>
            <span className='fw-bold'>4+ Million</span>
            <span className='small'>Readers of our articles</span>
          </div>

          <div className='d-flex flex-column col-6 col-sm-4'>
            <span className='fw-bold'>7+ thousand</span>
            <span className='small'>Followers on social media</span>
          </div>
        </div>
      </div>

      <div
        className={`d-none d-md-flex flex-md-row flex-column col-12 col-xl-5 pe-0 ps-0 position-relative ${styles.image_container}`}
      >
        <div className='d-flex flex-column justify-content-center align-items-center h-100 w-100 position-relative overflow-visible'>
          <img
            className={styles.globe}
            src={banner}
            alt='Globe pointing some of the countries that have already joined taik'
          />
        </div>
      </div>
    </div>
  </div>
);

export default JoinUs;
