import React, { useEffect, useState } from 'react';
import Slider from 'react-slick';
import { Heading } from '@commons';
import * as styles from './index.module.scss';

const Mentions = () => {
  const [mentions, setMentions] = useState([]);

  const carouselSettings = {
    autoplay: true,
    dots: false,
    accessibility: true,
    infinite: true,
    speed: 6000,
    slidesToShow: 3,
    slidesToScroll: 1,
    arrows: false,
    cssEase: 'linear',
    // lazyLoad: 'progressive',
    placeholders: false,
    adaptiveHeight: false,
    variableWidth: true,
    responsive: [
      {
        breakpoint: 1200,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          infinite: true,
          dots: false,
        },
      },
      {
        breakpoint: 992,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
          initialSlide: 2,
        },
      },
      {
        breakpoint: 576,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
        },
      },
    ],
  };

  useEffect(() => {
    const fetchMentions = async () => {
      const ments = [
        { title: 'Forbes', url: `https://theafricaiknow.org/images/forbes-icon.svg` },
        {
          title: 'Business Insider',
          url: `https://i.insider.com/5788e5f312df98da086adea2`,
        },
        {
          title: 'Nature',
          url: `https://media.springernature.com/full/nature-cms/uploads/product/nature/header-86f1267ea01eccd46b530284be10585e.svg`,
        },
        { title: 'Thomson Reuters', url: `https://theafricaiknow.org/images/thomson-reuters.svg` },
        { title: 'Global Citizen', url: `https://theafricaiknow.org/images/global-citizen.png` },
        {
          title: 'Princeton Alumni Weekly',
          url: `https://paw.princeton.edu/sites/all/themes/paw/images/paw-logo.svg`,
        },
        {
          title: 'Princeton University',
          url: `https://www.princeton.edu/themes/custom/tony/logo.svg`,
        },
      ];
      setMentions(ments);
    };

    fetchMentions();
  }, []);

  return (
    <section className='d-flex flex-column sections pb-0'>
      <div className='container'>
        <Heading secondary='Our' primary='Mentions' color='altdarker' />

        <Slider {...carouselSettings}>
          {mentions &&
            mentions.map((mention, index) => {
              const { url, title } = mention;
              return (
                <div key={index} className={`${styles.mention_container}`}>
                  <img
                    src={url}
                    alt={title}
                    title={title}
                    className={`img-fluid ${styles.mention_logo}`}
                  />
                </div>
              );
            })}
        </Slider>
      </div>
    </section>
  );
};

export default Mentions;
