import React from 'react';
import { FaQuoteLeft, FaTimes } from 'react-icons/fa';

import * as styles from './TAIK54CardPopUp.module.scss';

const TAIK54PopUp = ({ selectedInspiringPerson, isPopupVisible, closePopup }) => {
  const { name, country, description, quote, countryFlag, image } = selectedInspiringPerson;

  return (
    <>
      <div className={`${styles.overlay}`} style={{ display: isPopupVisible ? 'block' : 'none' }}>
        <div className={`card border-0 ${styles.card}`}>
          <div
            className={`article_image ${styles.card_image}`}
            style={{ backgroundImage: `url(${image})` }}
          >
            <button type='button' className={styles.close} onClick={() => closePopup()}>
              <FaTimes size={24} />
            </button>

            <div className={styles.country}>
              {country}
              <div className={styles.flag} style={{ backgroundImage: `url(${countryFlag})` }} />
            </div>
          </div>

          <div className={`border-0 ${styles.card_content}`}>
            <h4 className={styles.name}>{name}</h4>
            <hr className={styles.divider} />

            <div
              className={`${styles.description}`}
              dangerouslySetInnerHTML={{ __html: description }}
            />

            <div className={`${styles.quote}`}>
              <div>
                <FaQuoteLeft className={`${styles.quote_icon}`} size={24} />
              </div>
              <p>{quote}</p>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default TAIK54PopUp;
