import React from 'react';
import moment from 'moment';

import * as styles from './SingleArticle.module.scss';

const SingleArticleBannerDev = ({ articleData }) => {
  const { title, author, date, category, coverImageCredits } = articleData;

  return (
    <>
      <div className='container'>
        <div className='column'>
          <h1 className={`${styles.title}`}>{title}</h1>

          <div className={`bg-white rounded-pill ${styles.banner_divider}`} />

          <p className={`${styles.author}`}>
            By {author} &bull; {moment({ date }).format('MMMM DD, YYYY')}
          </p>

          <span className={`rounded-pill ${styles.category}`}>#{category}</span>
        </div>
      </div>
      <span className={`${styles.cover_credits}`}>Photo credit: {coverImageCredits}</span>
    </>
  );
};

export default SingleArticleBannerDev;
