import React from 'react';

import * as styles from './PeopleCard.module.scss';

const PeopleCard = ({ volunteer }) => {
  const { role, country, image, name } = volunteer;

  return (
    <div className={`card border-0 ${styles.card}`}>
      {image ? (
        <div
          className={styles.card_image}
          style={{ backgroundImage: `url(${image.childImageSharp.fluid.src})` }}
        />
      ) : (
        ''
      )}
      <div className={styles.card_content}>
        <p className={styles.name}>{name}</p>
        <p className={styles.role}>{role}</p>
        <p className={styles.country}>{country}</p>
      </div>
    </div>
  );
};

export default PeopleCard;
