import React, { useEffect, useState } from 'react';
import faker from 'faker';
import TAIKWebinarsRow from './TAIKWebinarsRow';
import LatestVideos from './LatestVideos';
// import * as styles from './index.module.scss';

const TAIKWebinars = () => {
  const [featuredPodcasts, setFeaturedPodcasts] = useState([]);

  useEffect(() => {
    const fetchWebinars = async () => {
      try {
        const podcasts = [];

        for (let index = 0; index < 2; index++) {
          const title = faker.lorem.paragraph();
          const image = faker.image.imageUrl();
          const description = `
          <p>
            "Connect The Dots with Takalani M." is an informative and thought-provoking exploration of all things Science, Technology, Engineering, Mathematics aka STEM for and by Africans. STEM and all support services have revolutionized every aspect of our lives and Africa has no shortage of practitioners and pioneers advancing that revolution.
          </p> 

          <p>
            On the show we go deep and wide to understand imaginative ideas, creative solutions, and technically robust innovations being championed by Africans across the globe in the field of STEM. Picture yourself in a university town, on a bench, under a tree planted hundreds of years ago listening to great ideas, retellings of a journey, wise counsel and then walking away with resources and actionable tips. Read. Set. Listen!
          </p>

          <p>
            On the show we go deep and wide to understand imaginative ideas, creative solutions, and technically robust innovations being championed by Africans across the globe in the field of STEM.
          </p>
          `;
          const hasTag = faker.random.arrayElement(['AfricaDayWithTAIK', 'TAIKTalksReparations']);

          podcasts.push({ title, hasTag, image, description });
        }
        setFeaturedPodcasts(podcasts);
      } catch (error) {
        console.error(error);
      }
    };

    fetchWebinars();
  }, []);

  return (
    <>
      <section className='sections bg-altsuperlight'>
        <div className='container'>
          {featuredPodcasts &&
            featuredPodcasts.map((featuredPodcast, index) => (
              <TAIKWebinarsRow
                key={index}
                even={(index + 1) % 2 === 0}
                featuredPodcast={featuredPodcast}
              />
            ))}
        </div>
      </section>

      <LatestVideos />
    </>
  );
};

export default TAIKWebinars;
