import React, { useEffect, useState } from 'react';
import Slider from 'react-slick';
import faker from 'faker';

import { Heading } from '@commons';

import EpisodeCard from './EpisodeCard';

const LatestEpisodes = () => {
  const [episodes, setEpisodes] = useState([]);

  const carouselSettings = {
    autoplay: true,
    accessibility: true,
    infinite: true,
    autoplaySpeed: 6000,
    speed: 900,
    slidesToShow: 3,
    slidesToScroll: 3,
    arrows: false,
    lazyLoad: 'progressive',
    centerMode: false,
    dots: true,
    responsive: [
      {
        breakpoint: 1366,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 1,
          dots: true,
        },
      },
      {
        breakpoint: 576,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          dots: false,
        },
      },
    ],
  };

  useEffect(() => {
    const fetchEpisodes = async () => {
      try {
        const pubs = [];

        for (let index = 0; index < 9; index++) {
          const category = faker.random.arrayElement(['Educate', 'Inform', 'Inspire']);
          const title = faker.lorem.sentence();
          const description = faker.lorem.paragraph();
          const image = faker.image.imageUrl();
          const author = faker.name.findName();
          const date = faker.date.past();

          pubs.push({ category, title, description, image, author, date });
        }

        setEpisodes(pubs);
      } catch (error) {
        console.error(error);
      }
    };

    fetchEpisodes();
  }, []);

  return (
    <section className='d-flex flex-column sections'>
      <div className='container'>
        <Heading secondary='Latest' primary='Episodes' color='altdarker' />

        <Slider {...carouselSettings}>
          {episodes &&
            episodes.map((episode, index) => <EpisodeCard key={index} episode={episode} />)}
        </Slider>
      </div>
    </section>
  );
};

export default LatestEpisodes;
