import React from 'react';
import { Link } from 'gatsby';
import moment from 'moment';
import _ from 'lodash';

import * as styles from './PublicationCard.module.scss';

const PublicationCard = ({ articleData }) => {
  const { title, image_url, date, category, slug, author, excerpt } = articleData.node.frontmatter;
  // const { title, image_url, date, category, author, excerpt } = articleData.node.frontmatter;

  return (
    <div className={`card h-100 ${styles.card_body}`}>
      <div
        style={{ backgroundImage: `url(${image_url.childImageSharp.fluid.src})` }}
        className={`card-img-top img-fluid ${styles.card_image}`}
        alt={title}
      />

      <div
        className={`card-header border-0 bg-transparent position-relative ${styles.card_content}`}
      >
        <span className='text-altlight'>{moment(date).format('MMMM DD, YYYY')}</span>

        <button
          type='button'
          className={`bg-altlighter text-altdark border-0 rounded-pill ${styles.category_pill}`}
        >
          {`#${category}`}
        </button>
      </div>

      <div className={`card-body border-0 ${styles.card_content} ${styles.card_details}`}>
        <h5 className='card-title'>
          <Link className='text-reset text-decoration-none' to={slug}>
            {title}
          </Link>
        </h5>

        <p className='card-text'>{_.truncate(excerpt, { length: 100 })}</p>
      </div>

      <div className='card-footer border-0 text-end text-altlight bg-transparent pb-4 px-4'>
        <span>{`By `}</span>
        <span className='fw-bold'>{author}</span>
      </div>
    </div>
  );
};

export default PublicationCard;
