import { Link } from 'gatsby';
import React from 'react';
import moment from 'moment';

import * as styles from './InterviewCard.module.scss';

const InterviewCard = ({ interview }) => {
  const { category, title, image_1, author, date, slug } = interview.node.frontmatter;
  // const { category, title, image_1, author, date } = interview.node.frontmatter;

  return (
    <div className={`card border-0 ${styles.card_body}`}>
      <div
        className={`interview_image w100 ${styles.card_image}`}
        style={{ backgroundImage: `url(${image_1.childImageSharp.fluid.src})` }}
      />

      <div
        className={`card-header border-0 bg-transparent position-relative ${styles.card_header}`}
      >
        <span className='text-altlight'>{moment(date).format('MMMM DD, YYYY')}</span>

        <button
          type='button'
          className={`bg-altlighter text-altdark border-0 rounded-pill ${styles.category_pill}`}
        >
          {`#${category}`}
        </button>
      </div>

      <div className={`card-body border-0 ${styles.card_content}`}>
        <h5 className='card-title mb-3'>
          <Link className='text-decoration-none text-altdarker' to={slug}>
            {title}
          </Link>
        </h5>
      </div>

      <div className='card-footer border-0 text-end text-altlight bg-transparent'>
        <span>{`By `}</span>
        <span className='fw-medium'>{author}</span>
      </div>
    </div>
  );
};

export default InterviewCard;
